
.page {
  display: flex;
  flex-direction: column;
  justify-content: center;
}


.meteo-courante {
  display: flex;
  flex-direction: row;
  justify-content: center;
}


.precipitation {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.meteo-prochaine {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.meteo-prochaines-heures {
  display: flex;
  flex-direction: row;
  justify-content: center;
}


.meteo-prochains-jours {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.charts {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.aujourd-hui{
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.jour-parent {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 0.3fr 1fr 0.3fr;
  grid-column-gap: 4px;
  grid-row-gap: 4px;
}

.jour-matin-label { grid-area: 1 / 1 / 2 / 2; }
.jour-apresMidi-label { grid-area: 1 / 2 / 2 / 3; }
.jour-matin-statut { grid-area: 2 / 1 / 3 / 2; }
.jour-apresMidi-statut { grid-area: 2 / 2 / 3 / 3; }
.jour-temperature { grid-area: 3 / 1 / 4 / 3; }

.prochains-jours{
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.meteo-prochains-jours-resume{
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.meteo-prochains-jours-resume-journee {
  display: flex;
  flex-direction: column;
}
